<template>
    <div>
        <div  v-if="pokemon">
            <h3>{{ pokemon.name }}</h3>
            <div>
                <img :src="pokemon.sprites.front_default" alt="" srcset="">
                <img :src="pokemon.sprites.back_default" alt="" srcset="">
            </div>
            <h3>types</h3>
            <div v-for="type in pokemon.types" :key="type.type.name">
                <h5>{{ type.type.name }}</h5>
            </div>
        </div>
    </div>
</template>

<script>
const axios = require("axios"); 

export default {
    name:"About",
    data() {
        return {
            pokemon: null
        }
    },
    created() {
        axios.get(`https://pokeapi.co/api/v2/pokemon/${this.$route.params.id}`)
        .then(response => {
            this.pokemon = response.data;
        })
    }
}
</script>