<template>
  <div>
    <div>
      <input type="text" placeholder="nhap ten pokemon" v-model="text"/>
    </div>
    <div v-for="pokemon in filterPoke" :key="pokemon.name">
      <router-link :to="`/about/${idLookup[pokemon.name]}`">{{ pokemon.name }}</router-link>
    </div>
  </div>
</template>

<script>
const axios = require('axios');

export default {
    name:"Home",
    data() {
      return {
        pokemons: [],
        text: "",
        idLookup: []
      }
    },
    computed: {
      filterPoke(){
        if(!this.text){
          return [];
        }else{
          return this.pokemons.filter(pokemon => pokemon.name.includes(this.text));
        }
      }
    }
    ,
    created() {
      axios.get("https://pokeapi.co/api/v2/pokemon?limit=1000&offset=0")
        .then(response => {
          this.pokemons = response.data.results;
          this.idLookup = response.data.results
            .reduce((acc, cur) => {
              const urlPart = cur.url.split("/");
              return {
                ...acc,
                [cur.name]: urlPart[urlPart.length-2]
              }
            },{});
            console.log(this.idLookup);
        });
    }
}
</script>